<template>
  <Breadcrumb
    class="mb-8 mt-4"
    pageTitle="Add Inventory"
    :backTo="{ name: 'inventory' }"
    :items="breadcrumb">
    <template #right>
      <Button color="teal" :loading="loading" @click="createInventory">Save</Button>
    </template>
  </Breadcrumb>
  <Card>
    <FormCreate ref="refFormCreate" :data="data" />
  </Card>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { useCreate } from 'composable/inventory'
import { useUser } from 'composable/user'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import FormCreate from 'components/Inventory/FormCreate.vue'
import Breadcrumb from 'components/Breadcrumb.vue'

export default defineComponent({
  components: {
    Button,
    Card,
    FormCreate,
    Breadcrumb,
  },
  setup() {
    const router = useRouter()
    const refFormCreate = ref(null)
    const { data, loading, action } = useCreate()
    const { me } = useUser()

    // if (me.value.is_datamatch_processing == 1) {
    //   router.push({
    //     name: 'inventory'
    //   })
    // }

    const createInventory = () => {
      const isValid = refFormCreate?.value?.validate() ?? false
      if (!isValid) return

      action(response => {
        router.push({
          name: 'inventoryEdit',
          params: {
            inventoryId: response.id
          }
        })
      })
    }

    const breadcrumb = [{
      text: 'Inventory',
      to: {
        name: 'inventory'
      }
    }, {
      text: 'Add Inventory'
    }]

    return {
      data,
      me,
      loading,
      createInventory,
      breadcrumb,
      refFormCreate
    }
  }
})
</script>